import { Injectable, Inject } from '@angular/core';

import { Cookie } from './Cookie';


@Injectable()

export class UserSessionService {

    loginInfor = {
        ticket: null,
        user: null
    }

    subcodeInfo = {
        user: null,
        activeTokens: null,
        token: null,
        subcode: null
    }

    initLoginInfo() {
        this.loginInfor = {
            ticket: null,
            user: null,
        }
    }

    initSubcodeInfo() {
        this.subcodeInfo = {
            user: null,
            activeTokens: null,
            token: null,
            subcode: null
        }
    }

    EXPIRATION_DAYS = 2;
    ROLE = {
        USER: 0x00,
        ADMIN: 0x01,
        SUBCODE: 0x02
    }

    constructor(private cookieService: Cookie) { }

    /**
     Login Information
     */

    isLoggedIn() {
        let session = this.cookieService.getValueSession();
        if (session) {
            this.loginInfor.ticket = session.ticket;
            this.loginInfor.user = session.user.general;
            return true;
        }
        return false;
    }

    setLoginInfo(login, stay) {
        this.loginInfor.ticket = login.ticket;
        this.loginInfor.user = login.user.general;
        this.cookieService.setValueSession(login, stay);
    }

    getLoginUserFullname() {
        let sn = this.loginInfor.user.sn;
        let gn = this.loginInfor.user.givenName;
        let name = this.getLoginUSername();
        if (sn | gn) {
            name = sn + ' ' + gn;
        }
        return name;
    }

    getLoginUSername() {
        return this.loginInfor.user.account;
    }

    getTicket() {
        if (this.loginInfor.ticket === null) {
            return;
        }
        return this.loginInfor.ticket.code;
    }

    isAdmin() {
        let admin = false;
        if (this.loginInfor.user.role & this.ROLE.ADMIN) {
            admin = true;
        }
        else if (this.loginInfor.user.role & this.ROLE.SUBCODE) {
            admin = true;
        }
        return admin;
    }

    clearLoginInfo() {
        this.initLoginInfo();
        this.cookieService.deleteValueSession();
    }

    /*
     Subcode Information 
     */
    setSubcodeUSer(user) {
        this.subcodeInfo.user = user;
    }

    setSubcodeActiveTokens(tokens) {
        this.subcodeInfo.activeTokens = tokens;
    }

    setSubcodeToken(token) {
        this.subcodeInfo.token = token;
    }
    setSubcodeCode(code) {
        this.subcodeInfo.subcode = code
    }
    getSubcodeInfo(){
        return this.subcodeInfo;
    }
    clearSubcodeInfo(){
        this.initSubcodeInfo();
    }
    clearAll() {
        this.initLoginInfo();
        this.initSubcodeInfo();
    }
    getPagesize(name) {
        return this.cookieService.getValuePagesize(name);
    }
    setPagesize(name, size) {
        this.cookieService.setValuePagesize(name, size);
    }

}
