import { Injectable, Injector, } from '@angular/core'
import { HttpInterceptor, HttpHandler, HttpEvent, HttpRequest, HttpErrorResponse, HttpResponse, HttpHeaders } from '@angular/common/http'
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { ErrorModel } from './ErrorModel';
import { Router } from '@angular/router';
import { UserSessionService } from './usersession.service';
import { jsonpCallbackContext } from '@angular/common/http/src/module';

@Injectable()
export class HttpErrorInterceptor implements HttpInterceptor {
	constructor(private userSessionService: UserSessionService) { }

	intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
		let ticket = this.userSessionService.getTicket();
		// request = request.clone({
		// 	headers: request.headers.set('Content-Type', 'application/json; charset=utf-8')
		// })
		// if (ticket !== '') {
		// 	request = request.clone({
		// 		headers: request.headers.set('x - mars - ticket', ticket)
		// 	})
		// }
		console.log(request);
		// request = request.clone({ headers: request.headers.set('x - mars - ticket', ticket)});
		console.log(request);
		return next.handle(request).pipe(catchError((httpErrorResponse: HttpErrorResponse) => {
			let message: ErrorModel
			switch (httpErrorResponse.status) {
				case 400: {
					message = { code: 400, message: "URL|()" };
					break;
				}
				case 404: {
					message = { code: 404, message: "(URL)" };
					break;
				}
				case 405: {
					message = { code: 405, message: "" };
					break;
				}
				case 403: {
					message = { code: httpErrorResponse.error.code, message: httpErrorResponse.error.msg };
					console.log(message);
					break;
				}
			}
			return throwError(message);
		}))

	}
}