import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl, ValidationErrors, ValidatorFn, AbstractControl } from '@angular/forms';
import  { Router, ActivatedRoute } from '@angular/router'
import { UserService, IUser } from '../shared/index';

@Component({
    selector: 'app-user-update',
    templateUrl: './user-update.component.html',
    styleUrls: ['./user-update.component.css',
    ],
})
export class UserUpdateComponent implements OnInit {
    isCreateUser = false;
    formCreateUser: FormGroup;
    user : IUser;
    userUpdate:  IUser = {username: '', phone: '', address: ''};
    constructor(private formBuilder: FormBuilder, private userService: UserService, 
    private router: Router, private route: ActivatedRoute) { }
    ngOnInit() {
        console.log("ngOnInit");
        let name = this.route.snapshot.paramMap.get('username');
        if (name && name.length > 0) {
            this.userService.getUserByUsername({'username':name}).subscribe(user => {
                if (user.length > 0) {
                    let temp : IUser = {username: user[0]['username'], phone: user[0]['phone'], address: user[0]['address']};
                    console.log(this.userUpdate);
                    this.userUpdate = temp;
                    console.log(this.userUpdate);
                    this.formCreateUser.get('username').setValue(this.userUpdate['username']);
                    this.formCreateUser.get('address').setValue(this.userUpdate['address']);
                    this.formCreateUser.get('phone').setValue(this.userUpdate['phone']);
                }
            })
        }


        this.formCreateUser = this.formBuilder.group({
            'username': [],
            'phone': ['', Validators.pattern('^([0-9]{3}[-][0-9]{3}[-][0-9]{4})$')],
            'address': [''],
        });
    }
    submitUpdateUser(value: Object) {
        if (this.formCreateUser.valid) {
            let user: IUser = { username: value['username'], address: value['address'], phone: value['phone'] };
            console.log(user);
            this.userService.updateUser(user).subscribe(check => {
                console.log("create user " + check);
                if (check) {
                    this.router.navigate(['/user-list-manager']);
                }
            });
        }
    }

}


