import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { IUser } from '../shared/interface';
import { UserService, LoginService } from '../shared/index' ;
import { Route, Router, ActivatedRoute } from '@angular/router';
import { MatDialog, MatDialogConfig } from '@angular/material';
import { MyDialogSpeComponent } from '../my-dialog-spe/my-dialog-spe.component';

@Component({
	selector: 'app-user-list',
	templateUrl: './user-list.component.html',
	styleUrls: ['./user-list.component.css',
	],
})
export class UserListComponent implements OnInit {

	userList: IUser[];
	constructor(private userService: UserService, private router: Router, private loginService: LoginService, private dialog: MatDialog) { }

	ngOnInit() {
		if (this.loginService.getInSession('login')) {
			this.userService.getAllUsers().subscribe(users => {
				if (users && users.length > 0) {
					console.log("User list:" +  users);
					this.userList = users;
					console.log("User list client:" + this.userList);
					this.loginService.savaInSession('userList', this.userList);
				}
			});
		} else {
			this.router.navigate(['/login']);
		}

	}
	deleteUser(username: string) {
		let dialogConfig = new MatDialogConfig();
		dialogConfig.disableClose = true;
		dialogConfig.autoFocus = false;
		dialogConfig.data = {
			username : username
		}
		let dialogRef = this.dialog.open(MyDialogSpeComponent, dialogConfig);
		dialogRef.afterClosed().subscribe(isDelete => {
			if (isDelete) {
				this.userService.deleteUser(username).subscribe(check => {
					if (check) {
						this.userService.getAllUsers().subscribe(userList => {
							this.userList = userList;
						});
					}
				});
			} 
		});
	}
	updateUser(username: string) {
		this.router.navigate(['/update-user', {username: username}])
	}
	showUserList = true;
	user_detail = "";
	createUser(username: string) {
		this.showUserList = false;
		this.user_detail  = username;
	}

	logout(value: Object) {
		this.loginService.removeInSession('login');
		this.loginService.removeInSession('username');
		this.loginService.removeInSession('userList');
		this.router.navigate(['/login']);
	}
}
