import { Injectable, Inject } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { IUser } from './interface'
import { LOCAL_STORAGE, WebStorageService } from 'angular-webstorage-service'
import { UserSessionService } from './usersession.service';


@Injectable()

export class LoginService {
    url = '/api/login';
    data: any[];
    headers = new HttpHeaders({
        'Content-Type': 'application/json',
        'Authorization': 'my-auth-token'
    });
    option = {
        headers: this.headers
    };

    constructor(private httpClient: HttpClient, @Inject(LOCAL_STORAGE) private storage: WebStorageService, private userSessionService: UserSessionService) { }
    /**
     * 
     * @param user : IUser
     * return true if login successful, otherwise false unsuccessful.
     */
    checkLogin(user: IUser): Observable<any> {
        let data =  {
            type: "plain",
            id: user['username'],
            pass: user['password']

        }
        let ticket = this.userSessionService.getTicket();
        let headers1 = new HttpHeaders();
        headers1 = headers1.append('Content-Type', 'application/json');
        if (ticket) {
            headers1 = headers1.append('x-mars-ticket', ticket);
        }
        
        this.option = {
            headers : headers1,
        }
        console.log(JSON.stringify(data));
        return this.httpClient.post(this.url, JSON.stringify(user), {
            headers : headers1
        });
    }

    /**
     * 
     * @param key : key
     * @param val : value
     * store in storage service
     */
    savaInSession(key: string, val: any): void {
        console.log('recieved: key ' + key + " value: " + val);
        this.storage.set(key, val);
        console.log(this.storage.get(key));
    }
    /**
     * 
     * @param key : key
     * return object from key
     */
    getInSession(key: string): any {
        console.log("getInSession");
        let temp = this.storage.get(key);
        console.log((temp));

        if (temp === 'undefined') {
            return false;
        }
        else {
            return temp;
        }

    }
    /**
     * 
     * @param key : key 
     * remove object from key in store service
     */
    removeInSession(key: string) {
        return this.storage.remove(key);
    }



}
