import { Injectable } from '@angular/core';
import { CookieService } from 'ngx-cookie-service';
import { Base64 } from './Base64';


@Injectable()

export class Cookie {

    session = "_mars_session";
    lang = "_mars_lang";
    pagesize = "_mars_pagesize";

    constructor(private cookieService: CookieService) { }

    defaultExpire() {
        let d = new Date();
        d.setFullYear(d.getFullYear() + 1);
        return d;
    }
    sessionExpire() {
        let d = new Date();
        d.setDate(d.getDate() + 1);
        return d;
    }
    daysToDate(days){
        let d = new Date();
        var e = new Date(d.getTime() + ((24 * 60 * 60 * 1000)*days));
        return e;
    }

    get(name: string) {
        return this.cookieService.get(name);
    }
    set(name: string, value: string, expire?: Date) {
        if (expire === undefined) {
            expire = this.defaultExpire();
        }
        return this.cookieService.set(name, value, expire);
    }
    delete(name){
        this.cookieService.delete(name);
    }
    getValueSession(){
        return JSON.parse(Base64.decode(this.get(this.session)))
    }
    setValueSession(json, stay){
        let expire = null;
        if (stay) {
            expire = this.sessionExpire();
        }
        return this.set(this.session, Base64.encode(JSON.stringify(json)), expire);
    }
    deleteValueSession(){
        return this.delete(this.session);
    }

    getValueLocal(){
        return this.get(this.lang);
    }
    setValueLocal(name: any){
        return this.set(this.lang, name);
    }
    deleteValueLocal(){
        return this.delete(this.lang);
    }
    getJsonPagesize(cookie, name, default_value) {
        let params = this.get(cookie);
        let value = default_value;
        if (params != null) {
            let plain = Base64.decode(params);
            let json = JSON.parse(plain);
            if (json[name] !== undefined) {
                value = json[name];
            }
        }
        return parseInt(value, 10);
    }
    setJsonPagesize(cookie, name, value) {
        let params = this.get(cookie);
        let json  = {};
        if (params) {
            let plain = Base64.decode(params);
            json = JSON.parse(plain);
        }
        json[name] = value;
        let string = JSON.stringify(json);
        let encode = Base64.encode(string);
        return this.set(cookie, encode);
    }


    getValuePagesize(name){
        return this.getJsonPagesize(this.pagesize, name, 25); 
    }
    setValuePagesize(name, value) {
        return this.setJsonPagesize(this.pagesize, name, value);
    }
    deleteValuePagesize(){
        return this.delete(this.pagesize);
    }









}
