import { NgModule, SkipSelf, Optional } from '@angular/core'
import { CommonModule } from '@angular/common'
import { LoginService } from './login.service';
import { UserService } from './user.service';
import { UserSessionService } from './usersession.service';
import { Cookie } from './Cookie';
import { CookieService } from 'ngx-cookie-service';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { HttpErrorInterceptor } from './HttpErrorInterceptor';
import { EnsureModuleLoadedOneceGuard } from './ensure-module-loaded-once.guard';
import { MAT_DIALOG_DEFAULT_OPTIONS, MAT_LABEL_GLOBAL_OPTIONS } from '@angular/material';
import { RouterModule } from '@angular/router';

@NgModule({
    imports: [HttpClientModule, CommonModule, RouterModule],
    exports: [HttpClientModule, CommonModule, RouterModule],
    providers: [LoginService, UserService, UserSessionService, Cookie, CookieService,
        {
            provide: HTTP_INTERCEPTORS,
            useClass: HttpErrorInterceptor,
            multi: true
        },
        {
            provide: MAT_DIALOG_DEFAULT_OPTIONS, useValue: { hasBackdrop: false }
        },
        { 
            provide: MAT_LABEL_GLOBAL_OPTIONS, useValue: { float: 'right' } 
        },
    ] // these should be singleton
})
export class CoreModule extends EnsureModuleLoadedOneceGuard { // Ensure that Core Module is only loaded into AppModule
    // Looks for the module in the parent injector to see if it's already been loaded (only want it loaded once)
    constructor(@Optional() @SkipSelf() parentModule: CoreModule) {
        console.log("once");
        super(parentModule);
    }
}