import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { IUser } from '../shared/index';
@Component({
    selector: 'child-user',
    template: `
    <div *ngIf="userInfor">

  <mat-table [dataSource]="userlist">
  <ng-container matColumnDef="username">
  <mat-header-cell *matHeaderCellDef>Username</mat-header-cell>
  <mat-cell *matCellDef="let element">{{element.username}}</mat-cell>
</ng-container>
<ng-container matColumnDef="password">
  <mat-header-cell *matHeaderCellDef>Address</mat-header-cell >
  <mat-cell mat-cell *matCellDef="let element">{{ element.password }}</mat-cell>
</ng-container>
<ng-container matColumnDef="address">
  <mat-header-cell *matHeaderCellDef>Address</mat-header-cell >
  <mat-cell mat-cell *matCellDef="let element">{{ element.address }}</mat-cell>
</ng-container>
<ng-container matColumnDef="phone">
  <mat-header-cell *matHeaderCellDef>Phone</mat-header-cell >
  <mat-cell mat-cell *matCellDef="let element">{{ element.phone }}</mat-cell>
</ng-container>
<mat-header-row *matHeaderRowDef="['username', 'password', 'address','phone']"></mat-header-row>
<mat-row *matRowDef="let row: columns: ['username', 'password', 'address','phone']"></mat-row>


    // <thead>
    //   <tr>
    //     <th scope="col">Username</th>
    //     <th scope="col">Password</th>
    //     <th scope="col">Address</th>
    //     <th scope="col">Phone</th>
    //   </tr>
    // </thead>
    // <tbody>
    //   <tr>
    //     <th scope="row">{{userInfor['username']}}</th>
    //     <td>{{userInfor['password']}}</td>
    //     <td>{{userInfor['address']}}</td>
    //     <td>{{userInfor['phone']}}</td>
    //   </tr>
    // </tbody>
  </mat-table>
  </div>
    <button (click)="logouted()" mat-raised-button >Logout</button>`
    ,
})
export class ShowUserComponent {
    @Input() userInfor: IUser;
    userlist = [];
    @Output() logout = new EventEmitter<boolean>();
    ngOnInit() {
        console.log(this.userInfor['username']);
        var password = "";
        for (var i = 0; i < this.userInfor['password'].length; i++) {
            password += "*";
        }
        this.userInfor['password'] = password;
        this.userlist.push(this.userInfor);
        console.log(this.userlist);
    }
    logouted() {
        console.log('action child');
        this.logout.emit(true);
    }


}
