import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialog, MatDialogConfig, MatDialogRef } from '@angular/material'

@Component({
  selector: 'app-my-dialog-spe',
  templateUrl: './my-dialog-spe.component.html',
  styleUrls: ['./my-dialog-spe.component.css']
})
export class MyDialogSpeComponent implements OnInit {
  username: string;


  constructor(@Inject(MAT_DIALOG_DATA)private data:any, private dialogRef: MatDialogRef<MyDialogSpeComponent>) { 
    this.username = data.username;
  }

  ngOnInit() {
  }
  save(){ 
    this.dialogRef.close(true);
  }
  close(){
    this.dialogRef.close(false);
  }

}
