import { Component, OnInit, OnChanges, SimpleChanges, HostListener } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { LoginService, IUser } from '../shared/index'
import { Router } from '@angular/router'
import { ErrorModel } from '../shared/ErrorModel';
import { UserSessionService } from '../shared/usersession.service';
@Component({
    selector: 'cm-login-mobile',
    templateUrl: './login.component.mobile.html',
    styleUrls: ['./login.component.css',
    ],

})
export class LoginMobileComponent implements OnInit {
    username = "";
    password = "";
    userList = null;
    isError = false;
    isLogin = false;
    form: FormGroup;
    error: ErrorModel;
    innerWidth: any;
    isAdmin = false;
    ticket : any;
    user : any;
    constructor(private formBuilder: FormBuilder, private service: LoginService, private router: Router, private userSessionService: UserSessionService) { 
        this.innerWidth = window.innerWidth;
        this.onResize();
     }

    ngOnInit() {
        this.form = this.formBuilder.group({
            username: ['', Validators.required],
            password: ['', Validators.required]
        });
        // this.isLogin = this.service.getInSession('login');
        // this.username = this.service.getInSession('username');
        // this.userList = this.service.getInSession('userList');
        // if (this.isLogin != true) {
        //     this.isLogin = false;
        // } else {
        //     this.router.navigate(['/user-list-manager']);
        // }
        this.onChanges();
    }
    @HostListener('window: resize', ['$event'])
    onResize(event?) {
        this.innerWidth = window.innerWidth;
        console.log(this.innerWidth);
    }
    onChanges(): any {
        this.form.valueChanges.subscribe((val) => {
            this.isError = false;
        });
    }
    submitLogin(value: Object) {
        if (this.form.valid) {
            console.log(value);
            let user: IUser = { username: value['username'], password: value['password'] };
            this.username = value['username'];
            this.password = value['password'];
            this.service.checkLogin(user).subscribe(login => {
                console.log(login);

                    this.ticket = login.ticket;
                    console.log(JSON.stringify(this.user));
                    console.log(JSON.stringify(this.ticket));
                    this.userSessionService.setLoginInfo(login, true);
                    this.isAdmin = this.userSessionService.isAdmin();
                    this.user = this.userSessionService.getLoginUserFullname();
                    

            },
                error => {
                    this.error = error;
                    this.isError = true;
                    console.log(this.error);
                }

            );

        }
    }

    // logout(value: boolean) {
    //     console.log(value);
    //     if (value) {
    //         this.service.removeInSession('login');
    //         this.service.removeInSession('username');
    //         this.isLogin = false;
    //         this.isError = false;
    //     }
    // }
    // btnCreateUser() {
    //     this.router.navigate(['/create-user']);
    // }
    // changeDection(value: boolean) {
    //     if (value) {
    //         console.log("change detect parent");
    //         this.isError = false;
    //     }
    // }
}
